    // 
    const handleBatchApprove = () => {
      if (selectedFiles.value.length === 0) {
        ElMessage.warning('');
        return;
      }

      const pendingFiles = selectedFiles.value.filter(file => file.status === 'pending');

      if (pendingFiles.length === 0) {
        ElMessage.warning('');
        return;
      }

      ElMessageBox.confirm(
        ` ${pendingFiles.length} `,
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).then(() => {
        pendingFiles.forEach(file => {
          // 
          const originalFile = fileList.value.find(f => f.id === file.id);
          if (originalFile) {
            originalFile.status = 'approved';
          }
        });
        ElMessage.success(` ${pendingFiles.length} `);
        // 
        // this.$refs.fileTable.clearSelection(); // Vue 2 Vue 3  ref
      }).catch(() => {
        ElMessage.info('');
      });
    };

    // PC
    const addPcDialogVisible = ref(false);
    const availablePcs = ref([]); // PC
    const searchPcQuery = ref(''); // PC
    const selectedPcsToAdd = ref([]); // PC

    // PC (API)
    const fetchAvailablePcs = () => {
      const existingPcNumbers = pcDetailData.value.map(pc => pc.pcNumber);
      // 
      availablePcs.value = [
        { pcNumber: 'PC777777', appointmentParty: 'C', appointmentTime: '2024-03-10 10:00:00', appointmentNumber: 'BK777', startWarehouse: '', destination: '', palletCount: 5, weight: 1000, loadingType: '', boxCount: 80, priority: 1, volume: 2 },
        { pcNumber: 'PC888888', appointmentParty: 'D', appointmentTime: '2024-03-11 11:00:00', appointmentNumber: 'BK888', startWarehouse: '', destination: '', palletCount: 8, weight: 1800, loadingType: '', boxCount: 200, priority: 2, volume: 4 },
        { pcNumber: 'PC999999', appointmentParty: 'E', appointmentTime: '2024-03-12 15:30:00', appointmentNumber: 'BK999', startWarehouse: '', destination: '', palletCount: 12, weight: 3000, loadingType: '', boxCount: 150, priority: 3, volume: 6 },
        // PC
      ].filter(pc => !existingPcNumbers.includes(pc.pcNumber));
    };

    // PC
    const filteredAvailablePcs = computed(() => {
      if (!searchPcQuery.value) {
        return availablePcs.value;
      }
      return availablePcs.value.filter(pc => 
        pc.pcNumber.toLowerCase().includes(searchPcQuery.value.toLowerCase())
      );
    });

    // PC - 
    const handleAvailablePcSelectionChange = (selection) => {
      selectedPcsToAdd.value = selection;
    };

    // PC - 
    const handleConfirmAddPc = () => {
      if (selectedPcsToAdd.value.length === 0) {
        ElMessage.warning('PC');
        return;
      }
      // PCpcDetailData
      const pcsToAdd = selectedPcsToAdd.value.map(pc => ({
        //  availablePcs 
        pcNumber: pc.pcNumber,
        appointmentParty: pc.appointmentParty || '',
        appointmentTime: pc.appointmentTime || '',
        appointmentNumber: pc.appointmentNumber || '',
        startWarehouse: pc.startWarehouse || 'N/A', // 
        destination: pc.destination || 'N/A',
        palletCount: pc.palletCount || 0,
        weight: pc.weight || 0,
        loadingType: pc.loadingType || '', // 
        boxCount: pc.boxCount || 0,         // 
        priority: pc.priority || 3,         // 
        volume: pc.volume || 0,
        // /
        status: '',
        remark: '' 
      }));
      pcDetailData.value.push(...pcsToAdd);
      ElMessage.success(` ${pcsToAdd.length} PC`);
      addPcDialogVisible.value = false;
    };

    // PC - 
    const handleCancelAddPc = () => {
      addPcDialogVisible.value = false;
    };

    return {
      activeTab,
      basicInfo,
      pcDetailData,
      fileList,
      fileUploadDialogVisible,
      expenseActiveTab,
      allExpenses,
      deliveryExpenses,
      abnormalExpenses,
      waitingExpenses,
      loadingExpenses,
      otherExpenses,
      totalExpense,
      paidExpense,
      unpaidExpense,
      getStatusTagType,
      getPCStatusTagType,
      getFileTypeTagType,
      getFileTypeLabel,
      goBack,
      handleEdit,
      handleExport,
      handleAddPC,
      handleExportPC,
      handleViewPC,
      handleEditPC,
      handlePCCommand,
      showFileUploadDialog,
      handleFileUploadSubmit,
      handleFileUploadCancel,
      handlePreviewFile,
      handleDownloadFile,
      handleDeleteFile,
      getPaymentStatusTagType,
      handleAddExpense,
      handleExportExpense,
      handleEditExpense,
      handleDeleteExpense,
      expenseDialogVisible,
      currentExpense,
      handleExpenseSubmit,
      handleExpenseCancel,
      getExpenseStatusTagType,
      handleExpenseCommand,
      handleBatchPushExpense,
      selectedExpenses,
      handleSelectionChange,
      expenseTypes,
      getExpenseTypeLabel,
      getExpenseTypeTag,
      getPodStatusTagType,
      getPodStatusLabel,
      handleFileCommand,
      warehouseOptions,
      destinationOptions,
      supplierOptions,
      podDialogVisible,
      currentPodRow,
      handleUploadPod,
      handlePodSubmit,
      handlePodCancel,
      fileUploadWithTagVisible,
      currentFileRow,
      handleFileUploadWithTag,
      handleFileUploadWithTagSubmit,
      handleFileUploadWithTagCancel,
      handleBatchApprove,
      handleFileSelectionChange,
      selectedFiles,
      // PC
      addPcDialogVisible,
      searchPcQuery,
      filteredAvailablePcs,
      handleConfirmAddPc,
      handleCancelAddPc,
      handleAvailablePcSelectionChange
    };
  }
};
</script>

